import { Component, ViewChild, Renderer2, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';



@Component({
  selector: 'main-page',
  templateUrl: './main.html',
  styleUrls: ['main.scss']
})
export class MainPage {
  open = false;
  main = false;
  events = [];
  @ViewChild('content') content: ElementRef;
  @ViewChild('startButton') startButton: ElementRef;

  constructor(
    public router: Router,
    public renterer: Renderer2
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {
    let buttonStream$ = 
      Observable.fromEvent(this.startButton.nativeElement, 'click')
      .do(() => {
        this.startButton.nativeElement.className += "bounceOutUp animated";
        this.content.nativeElement.className += " blur";
      })
      .delay(2000)
      .subscribe(res => {
        this.router.navigate(['/guide'])
      })

    this.events.push(buttonStream$);
  }

  ngOnDestroy() {
    this.events.forEach(ele => ele.unsubscribe())
  }

}
