import { Component, ViewChild, Renderer2, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, merge } from 'rxjs';
import { mergeMap, map, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'carousel-ui',
  templateUrl: './carousel.html',
  styleUrls: ['carousel.scss']
})
export class CarouselUi {
  @ViewChild('carouselList') carouselList: ElementRef; 
  @Input() itemList: Array<any>;
  @Output() getIndex: EventEmitter<any> = new EventEmitter<any>();

  touchStart$;
  touchMove$;
  touchEnd$;
  itemWidth;
  listStartPosition = 0;

  constructor(
    public router: Router,
    public renderer: Renderer2
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {
    this.touchStart$ = Observable.fromEvent(this.carouselList.nativeElement, 'touchstart');
    this.touchMove$ = Observable.fromEvent(this.carouselList.nativeElement, 'touchmove');
    this.touchEnd$ = Observable.fromEvent(this.carouselList.nativeElement, 'touchend');
    this.itemWidth = this.carouselList.nativeElement.querySelector('.carousel-item').clientWidth;

    this.getIndex.emit(0);
    this.touchEventSet();
    this.touchEndEventSet();
  }

  touchEventSet(){
    this.touchStart$.pipe(
      mergeMap(start => {
        this.listStartPosition = this.carouselList.nativeElement.getBoundingClientRect().x;
        return this.touchMove$.pipe(
        takeUntil(this.touchEnd$),
        map(move => {
          return {start, move}
        })
      )}),
    ).subscribe(e => {
      let startPoint = e.start.changedTouches[0].pageX;
      let movePoint = e.move.changedTouches[0].pageX;
      let sub = movePoint - startPoint;
      this.translateX(this.carouselList, this.listStartPosition + sub);
    })

  }

  touchEndEventSet() {
    this.touchEnd$.subscribe(e => {
      let x = this.carouselList.nativeElement.getBoundingClientRect().x;
      let index = Math.floor(-(x - this.itemWidth / 2) / this.itemWidth)
      if(index < 0){
        index = 0
      }else if(index > this.itemList.length - 1){
        index = this.itemList.length - 1;
      }
      let resX = -index*this.itemWidth;
      this.getIndex.emit(index);
      this.updateIndicator(this.carouselList, index);
      this.translateX(this.carouselList, resX, 0.1);
    })
  }

  translateX(element:ElementRef, deltaX, duration = 0, callback = null) {
    element.nativeElement.style.transition = 'transform ' + duration + 's';
    element.nativeElement.style.transform = 'translate3d(' +  deltaX + 'px, 0, 0)';
    if (duration > 0 && callback) {
        element.nativeElement.addEventListener('transitionend', callback, {once: true})
    }
  }

  updateIndicator(element: ElementRef, index) {
    element.nativeElement.querySelector('.active').classList.remove('active');
    element.nativeElement.querySelector('.carousel-item:nth-child(' + (index + 1) + ')').classList.add('active');
  }
}
